.backgroundError {
  max-width: 100%;

  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 100vh;
  background-image: url(./bgheader.png);
  background-size: cover;
  background-position: bottom;
  background-repeat: no-repeat;
  background: linear-gradient(none);
  color: transparent;
  background-color: black;
}

.backgroundError h1 {
  font-family: "Montserrat", system-ui, -apple-system, BlinkMacSystemFont,
    "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
    sans-serif;

  font-size: 150px;
  text-align: center;
  color: #4959b7;
  margin-block-end: 0px;
}

.backgroundError p {
  margin-block-start: 0px;
  color: white;
  font-size: 38px;
  font-family: "Montserrat", system-ui, -apple-system, BlinkMacSystemFont,
    "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
    sans-serif;
  text-align: center;
  margin-block-end: 35px;
  margin-block-start: 0;
  padding-inline: 25px;
  max-width: 100%;
  line-height: 50px;
}

.backgroundError button {
  font-family: "Montserrat", system-ui, -apple-system, BlinkMacSystemFont,
    "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
    sans-serif;

  margin-block-start: 0;
}

.backgroundError a {
  text-align: center;
}

.backgroundError button {
  width: 236px;
  height: 46px;
  font-family: "Montserrat", system-ui, -apple-system, BlinkMacSystemFont,
    "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
    sans-serif;
  font-weight: 700;
  font-size: 12px;
  background: #4959b7;
  color: white;
  border: none;
  text-transform: uppercase;
  cursor: pointer;
}

@media (max-width: 500px) {
  .backgroundError p {
    color: white;
    font-size: 20px;
    font-family: "Montserrat", system-ui, -apple-system, BlinkMacSystemFont,
      "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans",
      "Helvetica Neue", sans-serif;
    text-align: center;
    margin-block-end: 35px;
    margin-block-start: 0;
    padding-inline: 25px;
    max-width: 100%;
    line-height: 20px;
  }
}
